'use strict';

let slideNumber = 1;
const buttonNext = document.querySelector('button[name=slide-prev]');
const buttonPrev = document.querySelector('button[name=slide-next]');

function addDiv(n) {
  showDiv(slideNumber += n);
}

function showDiv(n) {
  const x = document.getElementsByClassName('benefit-mob__item');
  const y = document.getElementsByClassName('benefit-mob__pag');

  if (n > x.length) {
    slideNumber = 1;
  } else if (n < 1) {
    slideNumber = x.length;
  }

  for (let i = 0; i < x.length; i++) {
    x[i].style.opacity = '0';
    x[i].style.transition = 'opacity 0.5s';
    y[i].style.backgroundColor = '#cfeff0';

    if (i >= slideNumber) {
      x[i].style.transform = 'translateX(100%)';
    } else if (i <= slideNumber) {
      x[i].style.transform = 'translateX(-100%)';
    }
    x[i].style.transition = 'opacity 0.5s, transform 1s';
    y[i].style.backgroundColor = '#cfeff0';
  }

  x[slideNumber - 1].style.transform = 'translateX(0)';
  x[slideNumber - 1].style.opacity = '1';
  y[slideNumber - 1].style.backgroundColor = '#333';
}

showDiv(slideNumber);

buttonNext.addEventListener('click', () => {
  addDiv(1);
});

buttonPrev.addEventListener('click', () => {
  addDiv(-1);
});

let slideIndex = 0;
const buttonNext1 = document.querySelector('button[name=slider-feature-next]');
const buttonPrev1 = document.querySelector('button[name=slider-feature-prev]');

function addDivs1(n) {
  showDiv1(slideIndex += n);
}

function showDiv1(n) {
  const x = document.getElementsByClassName('features__feature--slide');
  const y = document.getElementsByClassName('features__pag');

  if (n > x.length) {
    slideIndex = 1;
  } else if (n < 1) {
    slideIndex = x.length;
  }

  for (let i = 0; i < x.length; i++) {
    x[i].style.display = 'none';
    y[i].style.display = 'none';
  }
  x[slideIndex - 1].style.display = 'flex';
  y[slideIndex - 1].style.display = 'inline-block';
}

showDiv1(slideIndex);

buttonNext1.addEventListener('click', () => {
  addDivs1(1);
});

buttonPrev1.addEventListener('click', () => {
  addDivs1(-1);
});
